.info-tray-basic-container {
  /* border-bottom: 1px solid rgb(239,239,239); */
  background-size: cover;
  background-position: center;
  height: 150px;
  position: relative;
  /* background: grey; */
  /* padding-bottom: 20px; */
}

.info-tray-title-block {
  position: absolute;
  height: 70px;
  bottom: -1px;
  left: 0;
  right: 0;
  /* background: white; */
  /* display: flex;
  justify-content: center; */
}

.info-tray-title-block-flex {
  width: 160px;
