.info-tray-title-block-name {
  width: 130px;
  padding-bottom: 3px;
  font-size: 18px;
  line-height: 13px;
  text-align: center;
  /* letter-spacing: 2.2px; */
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'EngraversGothicBold';
