  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  color: #575757;
  overflow: hidden; 
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.info-tray-header-container {
    padding: 0 20px;
    padding-top: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
