    height: 20px;
    font-weight: bold;
    font-family: 'EngraversGothicBold';
}

.info-tray-header-title {
  font-size: 18px;
  line-height: 13px;
  letter-spacing: 2.2px;
  text-transform: uppercase;
}

.info-tray-close-button-container {
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 20px;
  margin-top: 10px;
}

.info-tray-close-button {
  height: 20px;
  width: 20px;
}

.info-tray-address-and-image-container {
  padding: 0 20px;
  margin-top: 25px;
  display: -ms-flexbox;
  display: flex;
