.top-tray-container {
  position: absolute;
  z-index: 9999999;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  /* transform: translate3d(0px, -50px, 0px), */
}


