.carousel-container {
  position: relative;
  height: 130px;
  /* max-width: 960px; */
  margin: 0 auto;
  overflow: hidden;
  -ms-touch-action: pan-y;
      touch-action: pan-y;
  border-bottom: 1px solid #efefef;
  padding-bottom: 5px;
}

.carousel-track {
  margin-left: 10px;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}

.carousel-card {
  text-decoration: none;
  padding: 10px;
  /* margin-right: 10px; */
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
  -webkit-tap-highlight-color: transparent;
}

.carousel-card-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /* background: green; */
  width: 130px;
  height: 120px;
  /* border-radius: 5px; */
}

.carousel-image-container {
  width: 100px;
  height: 60px;
  background: #f5f5f5;
}

.carousel-image {
  width: 100px;
  height: 60px;
}

.carousel-title {
  font-family: 'EngraversGothicBold';
  font-weight: bold;
  font-size: 14px;
  line-height: 13px;
  /* letter-spacing: 1.8px; */
  padding-top: 5px;
  overflow: hidden; 
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  text-transform: uppercase;
}

.carousel-subtitle {
  font-size: 12px;
  color: rgba(87, 87, 87, 0.6);
  overflow: hidden; 
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.carousel-header {
    font-family: 'EngraversGothicBT', sans-serif;
    /* font-weight: 300; */
    margin-left: 20px;
    font-size: 14px;
    line-height: 13px;
    letter-spacing: 1.8px;
    padding-top: 10px;
    padding-bottom: 10px;
}


.carousel-pin {
  height: 20px;
  width: 25px;
}

.carousel-pin-container {
  height: 20px;
  width: 25px;
  margin-top: -3px;
}

.carousel-left-side {
  width: 25px;
  position: relative;
}

.carousel-right-side {
  margin-left: 5px;
  width: 125px;
}

.carousel-index {
  position: absolute;
  top: -1px;
  right: 0;
  left: 0;
  text-align: center;
  color: white;
}




